  align-items: center;
  justify-content: center;
  margin-bottom: 32px;
  font-weight: 600;
  font-size: 24px;
  line-height: 1.4;
}
.delivery__tabs li {
  border-bottom: 2px solid transparent;
  padding: 11px 16px;
  transition: color 0.225s ease-in-out, border-color 0.225s ease-in-out;
  cursor: pointer;
  user-select: none;
}
.delivery__tabs li:not(:last-child) {
  margin-right: 64px;
}
.delivery__tabs li.is-active {
  border-color: #313896;
  color: #313896;
}
.delivery__tab-content {
  display: none;
}
.delivery__tab-content.is-active {
  display: block;
}
.delivery__caption {
  margin-bottom: 32px;
  font-size: 16px;
  text-align: center;
  color: rgba(55, 55, 55, 0.8);
}
.delivery__row {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  margin-bottom: 28px;
  gap: 28px;
}
.delivery__col {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 2px solid rgba(49, 56, 150, 0.1);
  padding: 30px;
  background: rgba(255, 255, 255, 0.8);
  transition: box-shadow 0.225s ease-in-out;
}
