}
.delivery__col-content {
  text-align: center;
}
.delivery__img picture {
  display: flex;
  align-items: center;
  justify-content: center;
}
.delivery__box {
  display: flex;
  justify-content: space-between;
  border: 2px solid rgba(49, 56, 150, 0.1);
  padding: 48px 69px 0 80px;
  background: rgba(255, 255, 255, 0.8);
}
